export default class Ball {

    constructor(options) {
        this.x = options.x || 10;
        this.y = options.y || 10;
        this.width = options.width || 15;
        this.height = options.height || 15;
        this.color = options.color || '#FFFFFF';
        this.speed = options.speed || 2;
        this.gravity = options.gravity || 2;
    }


    rebuildBlocks() {
        this.block1 = {
            x: this.x + ((this.width/2) - (this.width - this.width/3)/2),
            y: this.y + ((this.height/2) - (this.height - this.height/3)/2),
            width: this.width - this.width/3,
            height: this.height - this.height/3
        };
        this.block2 = {
            x: this.x,
            y: this.y + ((this.height/2) - (this.height - this.height/2)/2),
            width: this.width,
            height: this.height - this.height/2
        };
        this.block3 = {
            x: this.x + ((this.width/2) - (this.width - this.width/2)/2),
            y: this.y,
            width: this.width - this.width/2,
            height: this.height
        };
    }
}


