import Ball from "./ball";
import PGenerator from "./particles-generator";
import goToMainMenu from "./screens-changer";
import {playGameMusic, stopMusic} from './music-player';

const io = require('socket.io-client');
let socketAddress;
const EventEmitterCreator = require('events')
const EventEmitter = new EventEmitterCreator();
let server_cords;
let startTime;
let pause;
let stop;
let left;
let right;
let soundState = true;
let soundTrackLink;

const canvas = document.getElementById('canvas');
const ctx = canvas.getContext('2d');
const ParticlesGenerator = new PGenerator(ctx);
canvas.width = 650;
canvas.height = 400;
let score1 = 0;
let score2 = 0;
let keys = {}; let mouseCords = {y: 0}
let accelerometerGamma;
window.addEventListener('keydown', function (e) {
    keys[e.keyCode] = true;
    // e.preventDefault();
});
window.addEventListener('deviceorientation', ev => {
    if (ev.gamma) {
        accelerometerGamma = ev.gamma;
    }
}, true);
window.addEventListener('keyup', function (e) {
    delete keys[e.keyCode];
});
window.addEventListener('keydown', (e) => {
    if (!stop) {
        switch (e.keyCode) {
            case 80: {
                if (!stop) {
                    EventEmitter.emit('pause');
                }
                break;
            }
            case 13: {
                document.getElementById('chatSendButton').click();
            }
        }
    }
})
canvas.onmousemove = (e) => {
    mouseCords.y = e.clientY;
}

function Box(options) {
    this.x = options.x || 10;
    this.y = options.y || 10;
    this.width = options.width || 40;
    this.height = options.height || 50;
    this.color = options.color || '#FFFFFF';
    this.speed = options.speed || 2;
    this.gravity = options.gravity || 2;
}

var player1 = new Box({
    x: 10,
    y: 200,
    width: 15,
    height: 80,
    color: '#FFFFFF',
    gravity: 2
});
var player2 = new Box({
    x: 625,
    y: 100,
    width: 15,
    height: 80,
    color: '#FFFFFF',
    gravity: 2
});
var midLine = new Box({
    x: (canvas.width / 2) - 2.5,
    y: -1,
    width: 5,
    height: canvas.height + 1,
    color: '#FFFFFF'
});
let theBall = new Ball({
    x: (canvas.width / 2),
    y: (canvas.height / 2),
    width: 15,
    height: 15,
    color: '#FFFFFF',
    speed: 1,
    gravity: 1
})
let particlesArray = new Array();

function input() {
    if (server_cords) {
        if (right) {
            player1 = server_cords.player1;
        }
        if (left) {
            player2 = server_cords.player2;
        }
    }
    let player;
    if (left) {
        player = player1;
    } else if (right) {
        player = player2;
    }
    switch (localStorage.getItem('controls')) {
        case '0': {
            if (38 in keys) {
                if (player.y - player.gravity > 0) {
                    player.y -= player.gravity;
                }
            } else if (40 in keys) {
                if (player.y + player.height + player.gravity < canvas.height) {
                    player.y += player.gravity;
                }
            }
            break;
        }
        case '1': {
            const futurePlayerPos = (mouseCords.y - (player.height / 2));
            if (futurePlayerPos > 0) {
                if (futurePlayerPos + player.height < canvas.height) {
                    player.y = futurePlayerPos;
                }
            }
            break;
        }
        case '2': {
            const futurePlayerPos = ((canvas.height / 2) - (player.height / 2)) + accelerometerGamma * 5;
            if (futurePlayerPos > 0) {
                if (futurePlayerPos + player.height < canvas.height) {
                    player.y = futurePlayerPos;
                }
            }
            break;
        }
    }
}

function drawBox(box) {
    ctx.fillStyle = box.color;
    ctx.fillRect(box.x, box.y, box.width, box.height);
}

function drawBall(ball) {
    ctx.fillStyle = ball.color;
    ball.rebuildBlocks();
    ctx.fillRect(ball.block1.x, ball.block1.y, ball.block1.width, ball.block1.height);
    ctx.fillRect(ball.block2.x, ball.block2.y, ball.block2.width, ball.block2.height);
    ctx.fillRect(ball.block3.x, ball.block3.y, ball.block3.width, ball.block3.height);
}

function drawParticles() {
    particlesArray.forEach(item => {
        item.draw();
    });
}

function displayScore1() {
    ctx.font = "20px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    let str;
    // if (server_cords) { str = server_cords.score1 } else { str = score1 };
    str = score1;
    ctx.fillText(str, (canvas.width / 2) - 50, 30);
}

function displayScore2() {
    ctx.font = "20px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    let str;
    // if (server_cords) { str = server_cords.score2 } else { str = score2 };
    str = score2;
    ctx.fillText(str, (canvas.width / 2) + 50, 30);
}

function displayInfo(info) {
    ctx.font = "24px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    let str = info;
    ctx.fillText(str, (canvas.width / 2) - 175, (canvas.height / 2));
}

function displayNumbers(info) {
    ctx.font = "34px Arial";
    ctx.fillStyle = "rgb(255,255,255)";
    let str = info;
    ctx.fillText(str, (canvas.width / 2) - 10, (canvas.height / 2));
}

function ballBounce() {
    if (server_cords) {
        theBall.x = server_cords.theBall.x;
        theBall.y = server_cords.theBall.y;
    }
    if (((theBall.y + theBall.gravity + theBall.height) >= canvas.height)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'up');
    } else if (((theBall.y - theBall.gravity) <= 0)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'down');
    }
    ballCollision();
}

function playBeeps() {
    if (Math.round(Math.random() * 1) == 0) {
        document.getElementsByTagName('audio')[0].play().then(() => {
        });
    } else {
        document.getElementsByTagName('audio')[1].play().then(() => {
        });
    }
}

function playHit() {
    if (Math.round(Math.random() * 1) == 0) {
        document.getElementById('audio-hit1').play().then(() => {
        });
    } else {
        document.getElementById('audio-hit2').play().then(() => {
        });
    }
}

function ballCollision() {
    if (((theBall.x + theBall.speed - 5 <= player1.x + player1.width) && (theBall.y + theBall.gravity > player1.y) && (theBall.y + theBall.gravity <= player1.y + player1.height))) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'rht');
    } else if (((theBall.x + theBall.width + theBall.speed >= player2.x) && (theBall.y + theBall.gravity > player2.y) && (theBall.y + theBall.gravity <= player2.y + player2.height))) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'lft');
    } else if (theBall.x - theBall.speed < player1.x) {
        playBeeps();
    } else if (theBall.x + theBall.speed > player2.x + player2.width / 2) {
        playBeeps();
    }
    draw();
}

function draw() {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    displayScore1();
    displayScore2();
    drawBox(player1);
    drawBox(player2);
    drawBox(midLine);
    drawBall(theBall);
    drawParticles();
}

function loop() {
    if (!pause && !stop) {
        ballBounce();
        input();
        if (left) {
            EventEmitter.emit('sendCords', {player1: player1});
        } else if (right) {
            EventEmitter.emit('sendCords', {player2: player2});
        }
        window.requestAnimationFrame(loop);
    } else {
        if (pause) {
            let message = document.createElement('span');
            message.innerText = `Game paused by one of the players (Press 'P' to continue)`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
        }
    }
}

document.getElementById('chatSendButton').onclick = sendMessage;

function sendMessage() {
    if (document.getElementById('chatInput').value) {
        if (localStorage.getItem('name')) {
            EventEmitter.emit('sendMessage', `${localStorage.getItem('name')} says: ${document.getElementById('chatInput').value}`);
        } else if (left) {
            EventEmitter.emit('sendMessage', `player 1 says: ${document.getElementById('chatInput').value}`);
        } else if (right) {
            EventEmitter.emit('sendMessage', `player 2 says: ${document.getElementById('chatInput').value}`);
        }
        document.getElementById('chatInput').value = '';
    }
}

function gameStart(address) {
    socketAddress = address;
    if (socketAddress) {
        connect(socketAddress);
    } else {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        displayInfo('connect [ipV4]');
    }
    EventEmitter.on('connected', () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        displayInfo('Waiting other player to connect');
        EventEmitter.on('start', () => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo('start');
            stop = false;
            stopMusic();
            setTimeout(() => {
                let beep = document.getElementById('audio-beep3');
                beep.onended = () => { setTimeout(() => playGameMusic(), 250); }; beep.play();
            }, 250);
            loop();
        });
    });
}

function clearChat() {
    document.getElementById('chat').innerHTML = '';
}

function definePlayerSide() {
    ctx.font = "16px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    if (left) {
        let message = document.createElement('span');
        message.innerText = `You are from the LEFT side, use 'up' and 'down' to control your paddle.`;
        document.getElementById('chat').appendChild(message); message.scrollIntoView();
    } else if (right) {
        let message = document.createElement('span');
        message.innerText = `You are from the RIGHT side, use 'up' and 'down' to control your paddle.`;
        document.getElementById('chat').appendChild(message); message.scrollIntoView();
    }
}

function gameEnd() {
    if (!stop) {
        EventEmitter.emit('end_game', true);
    } else {
        EventEmitter.emit('end_game', false);
    }
}

function connect(address) {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    displayInfo(`Connecting to ${address}`);
    let socket = io.connect(address);

    socket.on('connect', () => {
        socket.on('player_left_game', (res) => {
            stop = true;
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo('Sorry but other player left the game!');
            socket.disconnect();
            socketAddress = undefined;
            stopMusic();
            setTimeout(() => {
                clearChat();
                goToMainMenu(true);
            }, 5000);
        })

        socket.on('connected', (res) => {
            EventEmitter.emit('connected');
        })

        socket.on('full_server', () => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo('Sorry but server is full, try again later!');
            socket.disconnect();
            stopMusic();
            setTimeout(() => {
                clearChat();
                goToMainMenu(true);
            }, 5000);
        });

        socket.on('server_cords', (res) => {
            server_cords = res;
        })

        socket.on('pong', (res) => {
            let latency = Date.now() - startTime;
            // document.getElementById('ping').innerText = `ping: ${latency}`;
        })

        socket.on('score', (res) => {
            score1 = res.score1;
            score2 = res.score2;
        })

        socket.on('start_position', (res) => {
            if (res == 'left') {
                left = true;
            } else if (res == 'right') {
                right = true;
            }
            definePlayerSide();
        })

        socket.on('start', () => {
            EventEmitter.emit('start');
            socket.emit('yes');
        })

        socket.on('pause', () => {
            if (!stop) {
                pause = true;
            }
        })

        socket.on('continue', () => {
            if (!stop) {
                pause = false;
                loop();
            }
        })

        socket.on('chat_update', (res) => {
            let message = document.createElement('span');
            message.innerText = res;
            document.getElementById('chat').appendChild(message);
            message.scrollIntoView();
        })

        socket.on('end_game', (res) => {
            stop = true;
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo(res);
            socket.disconnect();
            socketAddress = undefined;
            stopMusic();
            setTimeout(() => {
                clearChat();
                goToMainMenu(true);
            }, 5000);
        })

        EventEmitter.on('sendCords', (data) => {
            startTime = Date.now();
            socket.emit('cords', data);
        })

        EventEmitter.on('pause', () => {
            if (pause) {
                pause = false;
                loop();
                socket.emit('continue');
            } else {
                pause = true;
                socket.emit('pause');
            }
        })

        EventEmitter.on('add_score', (data) => {
            socket.emit('add_score', data);
        })

        EventEmitter.on('sendMessage', (data) => {
            socket.emit('chat_message', data);
        })

        EventEmitter.on('end_game', (changeMusic) => {
            stop = true;
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            socket.disconnect();
            socketAddress = undefined;
            stopMusic();
            clearChat();
            goToMainMenu(changeMusic);
        })
    })
}

export {gameStart, gameEnd}
